                                <v-layout row>
                                    <v-flex xs7>
                                        <div class="blog__card--headline">{{ post.title }}</div>
                                        <div class="blog__card--short-desc">{{ post.short_desc }}</div>
                                    </v-flex>
                                    <v-flex xs5>
                                        <visual :image="post.image" width="auto" height="auto" class="blog__card--image"></visual>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                            <v-card-actions class="blog__card--action">
                                <v-btn v-for="btn in post.buttons" :key="btn.class" :href="btn.href" target="_blank" icon>
                                    <svg v-if="btn.class === 'nuget'" class="svg-inline--fa fa-w-16 icon" style="font-size: 18px" version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="48px" height="48px" viewBox="0 0 48 48" enable-background="new 0 0 48 48" xml:space="preserve"><image id="image0" width="48" height="48" x="0" y="0" xlink:href="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAwCAMAAABg3Am1AAAABGdBTUEAALGPC/xhBQAAACBjSFJN AAB6JgAAgIQAAPoAAACA6AAAdTAAAOpgAAA6mAAAF3CculE8AAAB1FBMVEUAAACAgIAvNkosOENJ SUkrN0MqOEUpOEUrOUIrVVUqNkUpOEMrNkUqNUMrOEUqNkUXRkYrOEMrOEMmOUwpNkMqNkU2NlEp N0YpNkMqOUQrPEQpNkMrOEUpNkMnO0UsOUYsN0T///8zMz0pOkQoOUYrN0MpNkMoOEMqNkMrN0Qs NUVAQGApNkUpN0QrOEI5OVUzREQpOEIpOEMrOUMwQFArQFUrNkQrNUQrNkMpNkMpOEMpNkUuO0Mr OEUpOEMoOUMpOkYmPEcoNUUqNkUrN0UtPEsrOUcpNkUqOEUzTU0rM0QrNkMqNkMmOkMrOkYrNkUr OEUpNEMrN0IrN0VAQIArOEUrNkQsOEIrOEMrOEQjOkYpN0UqOEUpOkIqNkUrNkMnMkMqOEQrOEUp MEUnNEErNkMrNkQpNkUsOEIsOEQpNUYpNUUpOEUxPUksN0IoOUQsN0IrNkMvNEkpOUIsNUVVVVUp NkQpNkUpOUUuQEkrNkUrOUcrOEUrN0QrNkQrOEUrNkYtN0YpOUcqNUMqNUErOEMrOEUrNkUrOEMo NkQrNUYrNkMrNkUqOUUrNkUoNEcpNkUpOEQrNkYrNkUqOEYqN0QqN0QqOEQrOUczZmYqN0T///9T GazBAAAAmnRSTlMAAiZXB2vb+GwGaPL0XNe9C/vcG+zPE5r+hx6v/LAaY54BGUt5lZ2F1daGCGTa ZQkPjveQEAyWePrr4P096c5nLC9zq7oiNtSqCjzv5zVU9e5Fg6cExLh7m9IW38kf+cYu4XclO4rw 5pJpapTTFXQtRt0xUW8DpsGCHN4Sia20xXEzXoEr5Jzi9m2PoblV0FPxvF/jN7FPpSQFEn/rtwAA AAFiS0dEIcRsDRYAAAAHdElNRQfiBgoMJDs+XPQLAAACRUlEQVRIx5XW/V/SQBwH8G0SJKCVAi1a wBoYphRpM0LpUam0jAp71spKE3uiorIy7Zmezee+f23bFHd3u8X5+WnfL/d+bXfcDTiOF2pqBJ5j jmOD0+VybqxlBm4PaPHWMYN6MLJpMyvYsgIaGlmBbwX4A6xgq2iAbcxzCG6XAKQdIWbABcKRSJj5 gYzIMlbtVKKxpgiaWFQJyXZa2BV3gSWu5t0CdXhLawJs4tyz1zo+ua8NbNPWvp8cr8Yk+E+kDpWY /IGU3j6Y9nXSRSqK7+qujNY8dPhI8OixBrrIdGETPq61unuM6+wJujh5CgG9fQCnz6wW/WepIHcO Aee1xoV8Zb0G6LeIIU90UasvrZWX6eCKCa7GMdBEBx4TBK+hjxS4TgeiCQbT+rr1rlbhIZtvjwBw 46Z+zSu3gBFAYjh7+87dDDADEEdGPSKsA1Amuh6Q8t8bqyuM35cYQX3rA9k4tg9HmcDAo7Wt/7jI AJxPkK32tK8q6HyG7v7