                                    <v-icon v-else size="18px">{{ btn.class }}</v-icon>
                                </v-btn>
                                <v-tooltip right>
                                    <v-btn slot="activator" :to="`/read/${post.id}`" @click.native="onClick" icon>
                                        <v-icon size="18px">fab fa-readme</v-icon>
                                    </v-btn>
                                    <span class="blog__tooltip">Read more</span>
                                </v-tooltip>
                            </v-card-actions>
                        </v-card>
                        <div class="preview__meta">
                            <time class="preview__published">{{ prettyDate(post.published) }}</time>
                            <router-link class="preview__author" :to="`/by/${kebabify(post.author)}`" @click.native="scrollTo(0, 220, scrollDelay)">
                                {{ post.author }}
                            </router-link>
                        </div>
                    </v-flex>
                </v-layout>
            </li>
        </transition-group>
        <div id="chart">
            <div>
                <v-divider></v-divider>
            </div>
            <div class="blog__chart">
                <v-toolbar-title>Languages I use/have used</v-toolbar-title>
                <v-list-tile-sub-title>Chart with languages I use/have used and how many projects with those languages</v-list-tile-sub-title>
                <bar-chart v-model="chartData" :data="chartData"></bar-chart>
            </div>
        </div>
    </div>
</template>

<script>
    import { scrollTo, kebabify, prettyDate } from '../helpers'

    export default {
        name: 'blog-feed',
        resource: 'BlogFeed',
        props: {
            filters: {
                type: Object,
                default: () => {
                }
            }
        },
        data() {
            return {
                chartData: null,
                posts: [],
                transition: 'preview-appear',
                icons: [
                    { class: 'fab fa-facebook', href: 'https://www.facebook.com/pepijnvdbroek' },
                    { class: 'fab fa-twitter', href: 'https://twitter.com/kurozer0' },
                    { class: 'fab fa-github', href: 'https://github.com/kurozeropb' },
                    { class: 'fab fa-keybase', href: 'https"//keybase.io/kurozero' },
                    { class: 'fab fa-discord', href: 'https://discord.gg/Vf4ne5b' }
                ]
            };
        },
        computed: {
            reading() {
                return this.filters.post;
            },
            scrollDelay() {
                return (this.$device.phone) ? 0 : 560;
            },
            figureClass() {
                return { 'preview__figure--mobile': this.$device.phone && this.reading };
            },
            feed() {
                const filterBy = {
                    post: (filter, { id }) => filter === id,
                    author: (filter, { author }) => filter === this.kebabify(author)
                };

                if (!Object.keys(this.filters).length) return this.posts;

                return this.posts.filter(post => {
                    return Object.keys(this.filters).every(filter => {
                        return filterBy[filter](this.filters[filter], post)
                    });
                });
            }
        },
        methods: {
            scrollTo,
            kebabify,
            prettyDate,
            getBgImg(src) {
                return { backgroundImage: `url(${src})` };
            },
            stackPosts(posts) {
                let interval;
                const stack = () => {
                    this.posts.push(posts.shift());

                    if (!posts.length) {
                        this.transition = 'preview';
                        clearInterval(interval);
                    }
                };

                interval = setInterval(stack, 125);
            },
            onClick() {
                scrollTo(0, 220, this.scrollDelay);
            }
        },
        beforeMount() {
            this.$getResource('feed').then(posts => {
                if (!Object.keys(this.filters).length) {
                    this.stackPosts(posts);
                } else {
                    this.posts = posts;
                    this.transition = 'preview';
                }
            });

            this.$http.get('https://system.jeannedarc.xyz/github').then((response) => {
                this.chartData = response.data;
            }).catch((e) => {
                console.error(e);
            });

            /*
            this.chartData = [['Python', 2], ['C#', 4], ['TypeScript', 2], ['PHP', 1], ['JavaScript', 5], ['Kotlin', 2], ['CSS', 2], ['Elixir', 1], ['Go', 3], ['Ruby', 1], ['Vue', 1], ['HTML', 1], ['Rust', 1]];
            */
        }
    }
</script>



