import './startup'

import Vue from 'vue'
import router from '@/router'

import axios from 'axios';
import Vuetify from 'vuetify'
import VueAnalytics from 'vue-analytics'
import VueTyperPlugin from 'vue-typer'
import VueVisual from 'vue-visual'
import VueChartkick from 'vue-chartkick'
import Chart from 'chart.js'

import App from '@/App'
import resource from '@/plugins/resource'
import deviceQueries from '@/plugins/device-queries'
import * as resources from '@/resources'

import 'vuetify/dist/vuetify.min.css'
import 'vue-visual/index.css'

Vue.config.productionTip = false;

Vue.use(VueAnalytics, { id: 'UA-88870908-1', router });
Vue.use(Vuetify);
Vue.use(VueTyperPlugin);
Vue.use(VueChartkick, {adapter: Chart});
Vue.component('visual', VueVisual);
Vue.prototype.$http = axios;

Vue.use(resource, {
    resources,
    endpoint: '/static/api'
});

Vue.use(deviceQueries, {
    phone: 'max-width: 567px',
    tablet: 'min-width: 568px',
    mobile: 'max-width: 1024px',
    laptop: 'min-width: 1025px',
    desktop: 'min-width: 1280px',
    monitor: 'min-width: 1448px'
});

new Vue({
    el: '#app',
    router,
    ...App
});



